.app__footer{
    flex: 1;
    width: 120%;
    flex-direction: column;
    background-color: rgb(244, 249, 249);
}

.app__footer-cards{
    width: 60%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap-reverse;
    margin: 4rem 2rem 0rem;

    .app__footer-card{
        min-width: 290px;
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: center;

        margin: 0rem 0;
        padding: 0.95rem;
        border-radius: 10px;
        cursor: pointer;
        background-color: #ffebdf;

        transition: all 0.3s ease-in-out;

        img {
            width: 40px;
            height: 30px;
            object-fit: contain;
            margin: 0 0.7rem;
        }

        p {
            font-weight: 800;
        }

        a {
            text-decoration: none;
            font-weight: 550;
        }

        &:hover {
            box-shadow: 0 0 25px rgba(0,0,0,0.1);
        }

        @media screen and (max-width: 450px) {
            width: 100%;
        }
    }
    @media screen and (max-width: 768px) {
        width: 100%;
    }
}

.app__footer-cards .app__footer-card:last-child {
    background-color: #d8e3ff;
}

.app__footer-form{
    width: 60%;
    flex-direction: column;
    margin: 1rem 2rem;

    div, form{
        width: 90%;
        margin: 1rem 0;
        border-radius: 10px;
        cursor: pointer;
        align-items: center;
        flex-direction: column;

        transition: all 0.3s ease-in-out;
        input , textarea {
            width: 100%;
            padding: 0.95rem;
            border: none;
            border-radius: 7px;
            background-color: var(--white-color);

            font-family: var(--font-base);
            color: var(--secondary-color);
            outline: none;
            &:hover {
                box-shadow: 0 0 25px rgba(0,0,0,0.15);
            }
        }

        textarea {
            height: 170px;
        }
        
    }

    button {
        display: flex;
        padding: 1rem 2rem;
        border-radius: 10px;
        border: none;
        background-color: var(--secondary-color);
        font-weight: 500;
        color: var(--white-color);
        outline: none;
        margin: 2rem 0 0 0;
        font-family: var(--font-base);
        cursor: pointer;
    }

    @media screen and (max-width: 1200px) {
        width: 100%;
        margin: 1rem 0;
    }
}

.app__coffee {
    margin-top: 3rem;
}